.textarea{
  width: 100%;
  margin: 15px 0 5px 0;
  border: none;
  border-bottom: 1px solid #4caf50;
}

.textarea-container p{
  margin: 0;
  text-align: left;
}

.input-error textarea{
  border-bottom: 1px solid #f44336;
}

.input-error p {
  color: #f44336;
}

.textarea:focus {
  outline-width: 0;
}



