.chart {
    margin: 10px;
    padding: 10px 0 0 10px;
}

.chart .right {
    stroke: white;
    fill: #4caf50;
}

.chart .left {
    stroke: white;
    fill: #A5D6A7;
}

.chart rect:hover {
    fill: #64707d;
}

.chart text {
    fill: #5b5760;
}

.chart text.name {
    fill: #5b5760;
}

.chart text.title {
    fill: #2216ff;
}

.graph-legend span {
  display: inline-block;
  width: 20px;
  height: 20px;
  margin-right: 10px;
}

.graph-legend .practice{
  background-color: #4caf50;
}

.graph-legend .study{
  background-color: #a5d6a7;
}

.graph-legend p {
  margin: 0 0 5px 0;
}



