.project{
  width: 100%;
  border-bottom: 1px solid #c8c8c8;
  margin-bottom: 30px;
}

.project-image{
  margin-right: 20px;
}

.project-body{
  padding: 15px;
  width: 100%;
}

.project-title a{
  font-size: 2.0em;
  border: none !important;
  text-decoration: none;
}

.project-links a{
  margin-right: 10px;
}

.project-tags{
  text-align: center;
  margin-top: 20px;
}

.tag{
  padding: 0 5px 0 5px;
  font-size: 18px;
  color: #4caf50;
  margin: 0 0 5px 5px;
  border: 2px solid #4caf50;
}

@media (max-width: 550px) {
  .project-body h3{
    font-size: 1.3em;
  }
}

