html, body{
  margin:0;
  padding:0;
  height: 100%;
  font-family: Verdana, Geneva, sans-serif;
  font-weight: 100;
  font-size: 16px;
  color: #5b5760;
  -ms-touch-action: manipulation;
      touch-action: manipulation;
}

input[type='text'],
input[type='number'],
select,
textarea {
  font-size: 16px;
}



a{
  color: #4caf50;
  text-decoration: underline;
}

a:hover{
  cursor: pointer;
}

h1{
  font-size: 5.0em;
  font-weight: 100;
  margin: 0;
}

h2{
  font-size: 3.42em;
  margin: 15px;
  font-weight: 100;
}

h3{
  font-size: 1.14em;
  margin: 15px 0 15px 0;
  font-weight: 100;
}

.g-pad{
  margin: 25px 0 25px 0;
}

.u-fullwidth{
  width: 100%;
}



