.button{
  font-size: 0.8em;
  height: 33px;
  border: 3px solid #4caf50;
  border-radius: 2px;
  margin: 15px auto;
  color: #4caf50;
  background-color: #fff;
}

.button:hover{
  cursor: pointer;
}

.button:focus{
  outline-width: 0;
}

.primary{
  background-color: #4caf50;
  color: #fff;
}



